import { ref } from "@vue/reactivity";

const Search = ((props, { emit }) => {
    const selectedDrugs = ref([]);
    const listOfIDsOfSelectedDrugs = ref([]);
    const isInterOpened = ref(false)

    const getInteracitions = (() => {
        isInterOpened.value = true
    })

    const closeInteractions = (() => isInterOpened.value = false)

    const AddNewDrug = async(drug) => {

        if (!listOfIDsOfSelectedDrugs.value.includes(JSON.stringify(drug[0]))) {
            const data = await fetch(` http://188.166.82.170:3000/drugInfo?drugID=${drug[0]}&drugName=${drug[1]}`)
            const formated = await data.json();

            selectedDrugs.value.push(formated);
            listOfIDsOfSelectedDrugs.value.push(JSON.stringify(drug[0]));
        }
    }

    const closeTab = (id) => {
        // console.log(id);
        let index = listOfIDsOfSelectedDrugs.value.indexOf(id)
        listOfIDsOfSelectedDrugs.value.splice(index, index + 1)
        console.log(listOfIDsOfSelectedDrugs.value);
        ////////////////////
        let jednostava = JSON.parse(JSON.stringify(selectedDrugs.value))
        let index2
        for (let i = 0; i < jednostava.length; i++) {
            const element = jednostava[i];
            if (element["Drug"]["ID"] == id) {
                index2 = i
                break
            }
        }
        selectedDrugs.value.splice(index2, ((index2 == 0) ? 1 : index2))

    }

    return { AddNewDrug, selectedDrugs, closeTab, isInterOpened, getInteracitions, closeInteractions };
})

export default Search