<template>
  <div id="mainContainer" v-if="isOpened" @click.self="closeInter">
    <div id="centerBlock">
      <div class="close">
        <img :src="closeIcon" alt="" class="close" @click="closeInter" />
      </div>
      <h3>Sastojci</h3>
      <div class="ingredeintBox">
        <div class="listOfIngredients" v-for="ing in ingredients" :key="ing">
          <div class="singleIngredient">
            {{ ing }}
          </div>
        </div>
      </div>
      <h3>Interakcije</h3>
      <div v-if="interactions == 'empty'">Nejma, dobar si</div>
      <div class="" v-else-if="interactions">
        <div v-for="interaction in interactions" :key="interaction">
          {{ interaction }}
        </div>
      </div>
      <div v-else>Sabura momcino</div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { watch, watchEffect } from "@vue/runtime-core";

export default {
  props: ["isOpened", "drugs"],

  setup(props, { emit }) {
    const closeIcon = require("../assets/svg/close.svg");
    const active = ref();
    const interactions = ref(null);
    const ingredients = ref([]);

    watch(
      () => props.isOpened,
      () => {
        setIngArr();
        getInteractions();
      }
    );

    const getInteractions = async () => {
      const niz = (
        "http://188.166.82.170:3000/interactions?array=" +
        JSON.stringify(ingredients.value)
      ).toString();
      const data = await fetch(niz);
      interactions.value = await data.json();
      console.log(interactions.value);
    };
    const setIngArr = () => {
      ingredients.value = [];
      interactions.value = null
      let allDrugs = JSON.parse(JSON.stringify(props.drugs));
      allDrugs.map((d) => {
        let active = d["ActiveIngredients"];
        active.forEach((element) => {
          if (!ingredients.value.includes(element[1])) {
            ingredients.value.push(element[1]);
          }
        });
      });
    };

    const closeInter = () => {
      emit("closeInteractions");
    };

    return { closeInter, closeIcon, ingredients, interactions };
  },
};
</script>

<style lang="sass" scoped>
@import '../assets/SASS/buttonHover'

#mainContainer
    width: 100vw
    height: 100vh
    background-color: rgba(1, 1, 1, 0.6)
    position: absolute
    top: 0
    right: 0
    overflow: hidden

    display: flex
    justify-content: center
    align-items: center

    #centerBlock
        position: relative
        width: 80vw
        height: 80vh
        background-color: white
        padding: 35px 50px

        .close
            position: absolute
            right: 0
            top: 0
            margin: 5px
            padding: 5px
            height: 30px
            fill: white
            transition: 0.5s ease
            &:hover
                transition: 0.1
                height: 35px
                transform: translateX(2px)
                transform: translateY(-2px)
.ingredeintBox
  display: flex
  align-content: center
  justify-content: center
  flex-direction: row
  flex-wrap: wrap

  .listOfIngredients
    display: flex
    align-content: center
    justify-content: center
    flex-direction: row
    flex-wrap: wrap

    .singleIngredient
      width: fit-content
      text-transform: capitalize
      margin: 0px 5px 15px 5px
      font-size: 120%
      padding: 3px 20px
      // border: 1px black solid
      border-radius: 50px
      box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3)
</style>
