<template>

<div class="scrollableDropdown" >

    <div class="suggestionList" v-for="suggestion in suggestions" :key="suggestion">
        <div class="singleSuggestion" @click="selected(suggestion)">
            {{ suggestion[1] }}
        </div>
    </div>
</div>

</template>

<script>
import { defineComponent } from "@vue/runtime-core";

export default{
props:["suggestions"],

setup(props, {emit}){

    const selected = ((selected)=>{
        emit("custom", selected)

    })
    return {selected, }
}

}
</script>

<style lang="sass" scoped>

.scrollableDropdown
    background-color: white
    padding: 10px 0px
    width: calc(50vw)
    max-height: 145px
    overflow-y: scroll
    
    .suggestionList
        text-align: left

        .singleSuggestion
            padding: 3px 12px
            cursor: default
            transition: ease 0.8s

            &:hover
                transition: ease 0.1s
                background-color: rgba(230, 230, 230,0.8)

</style>
