<template>
  <div v-for="drug in listOfDrugs.slice().reverse()" :key="drug" class="drugList">

    <div class="singleDrug">
        <img :src="closeIcon" alt="" class="close" @click="closed(drug.Drug.ID)">
        <div class="drugName">
            {{ drug.Drug.Name }}
        </div>

        <div class="listOfIngredients">
            <div v-for="ing in drug.ActiveIngredients" :key="ing[0]" class="singleIngredient">
                {{ ing[1] }}
            </div>
        </div>
    </div>

  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { computed, watch } from "@vue/runtime-core";
export default {
  props: ["drugs"],
  emits:["closeDrugTab"],

  setup(props, { emit }) {
    const listOfDrugs = ref([]);
    const closeIcon = require("../assets/svg/close.svg")
    // removes duplicate ingredients
    watch(props.drugs, (drug) => {
      listOfDrugs.value = [];

      // listOfDrugs.value = JSON.parse('[{"Drug":{"ID":"783","Name":"QLAIRA 3 mg tableta  2 mg tableta  2 mg tableta  2 mg tableta  3 mg tableta  1 mg tableta"},"ActiveIngredients":[[243,"dienogest"],[244,"estradiol"]]},{"Drug":{"ID":"62","Name":"ZOLPAN 20 mg tableta"},"ActiveIngredients":[[25,"pantoprazole"]]},{"Drug":{"ID":"4246","Name":"Qlaira filmom oblo≈æene tablete"},"ActiveIngredients":[[1173,"estradiol"],[1190,"dienogest"]]}]')

      for (let i = 0; i < drug.length; i++) {
        const UniqList = Array.from(
          new Set(drug[i]["ActiveIngredients"].map(JSON.stringify)),JSON.parse);
        listOfDrugs.value.push(JSON.parse(JSON.stringify(drug[i])));
        listOfDrugs.value[i]["ActiveIngredients"] = UniqList;
      }
      // console.log("---")
    });

    const closed = ((id)=>{
        emit('closeDrugTab', id)
    })
    return { listOfDrugs, closeIcon, closed };
  },
};
</script>

<style lang="sass" scoped>
@import '../assets/SASS/buttonHover.sass'
.drugList
    width: 800px
    max-width: 80vw
    position: relative

    .singleDrug
        user-select: none
        display: flex
        // justify-content: flex-start
        align-items: center
        flex-direction: column

        position: relative
        background-color: white
        margin-bottom: 30px
        border-radius: 8px
        box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.2)

        .close
            position: absolute
            right: 0
            top: 0
            margin: 10px
            padding: 5px
            height: 25px
            fill: white
            transition: 0.5s ease
            &:hover
                transition: 0.1
                height: 30px
                transform: translateX(3px)
                transform: translateY(-3px)

        .drugName
            text-transform: lowercase
            font-size: 150%
            font-weight: 600
            padding: 20px 5vw
            &:first-letter
              text-transform: capitalize

        .listOfIngredients
            display: flex
            align-content: center
            justify-content: center
            flex-direction: row
            flex-wrap: wrap

            .singleIngredient
                width: fit-content
                text-transform: capitalize
                margin: 0px 5px 15px 5px
                font-size: 120%
                padding: 3px 20px
                // border: 1px black solid
                border-radius: 50px
                box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3)
</style>
