<template>
  <h1>
    welcome
  </h1>
  <div id="main">
    <AutocompleteSearch @addedADrug="AddNewDrug" />
    <div class="ispod">
      <button
        class="button-os"
        @click="getInteracitions"
        v-if="selectedDrugs.length > 0"
      >
        Test
      </button>
      <SelectedDrugCards :drugs="selectedDrugs" @closeDrugTab="closeTab" />
    </div>
  </div>
  <Interactions
    :isOpened="isInterOpened"
    @closeInteractions="closeInteractions"
    :drugs="selectedDrugs"
  ></Interactions>
</template>

<script>
import AutocompleteSearch from "../components/AutoComplete/AutocompleteSearch.vue";
import SelectedDrugCards from "../components/SelectedDrugCards.vue";
import Interactions from "../components/Interactions.vue";
import Search from "../javascript/search";

export default {
  components: { AutocompleteSearch, SelectedDrugCards, Interactions },
  emits: ["closeDrugTab"],
  setup(props, { emit }) {
    const {
      AddNewDrug,
      selectedDrugs,
      closeTab,
      isInterOpened,
      getInteracitions,
      closeInteractions,
    } = Search(props, { emit });

    return {
      AddNewDrug,
      selectedDrugs,
      closeTab,
      isInterOpened,
      getInteracitions,
      closeInteractions,
    };
  },
};
</script>

