<template>
  <div id="searchArea" >
    <input class="inputField" type="text" v-model="query"  @keyup="load" placeholder="Ukucajte naziv lijeka"/>
    <SuggestionList
      v-if="suggestions"
      :suggestions="suggestions"
      @custom="chosenADrugFromDropdown"
    ></SuggestionList>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import getSuggestions from "../../javascript/getSuggestions";
import SuggestionList from "../../components/AutoComplete/SuggestionList.vue";

export default {
  name: "AutocompleteSearch",
  components: { SuggestionList },
  setup(props, {emit}) {
    const { query, suggestions, load,chosenADrugFromDropdown } = getSuggestions(props, {emit});

    return { load, query, suggestions, chosenADrugFromDropdown};
  },
};
</script>

<style lang="sass">
#searchArea
  display: flex
  place-content: space-around flex-start
  align-items: center
  flex-direction: column

  //keeps it in place, so the dopdown does not move any content under it
  position: absolute
  left: 50vw
  transform: translateX(-50%)

  SuggestionList

  .inputField
    outline: none
    font-size: 150%
    padding: 10px 0px
    width: 877px
    max-width: 80vw

    text-align: center
    border-color: rgba(100, 181, 246, 0.5)
    border-radius: 10px
    box-shadow: none
    &::placeholder
      opacity: 0.4
</style>
