import { ref } from "@vue/runtime-core";

const getSuggestions = ((props, { emit }) => {

    let suggestions = ref();
    const error = ref(null);
    const query = ref("");

    const load = async() => {

        if (query.value) {
            const data = await fetch("http://188.166.82.170:3000/suggestion/" + query.value)
            suggestions.value = await data.json();
        } else {
            suggestions.value = null
        }
    };

    const chosenADrugFromDropdown = ((odabrani) => {
        suggestions.value = null
        query.value = null
        emit('addedADrug', odabrani)
    })



    return { query, suggestions, load, chosenADrugFromDropdown }
})

export default getSuggestions